import { Index } from '@upstash/vector'
import { Hono } from 'hono'

const index = new Index({
  url: 'https://sound-pheasant-10312-profanity-vector.upstash.io',
  token:
    'AB4FMHNvdW5kLXBoZWFzYW50LTEwMzEyLXByb2Zhbml0eWFkbWluWWpoa1pUQTFOV0V0TUdRNE5pMDBabVUyTFdJeE16WXRZMlU1TkdFeU4yRTVNV1V4',
})

const app = new Hono()

app.post('/', async (c) => {
  const body = await c.req.json()
  const { message } = body

  console.log('message', message)

  const result = await isProfanity(message)
  console.log('result????', result)

  async function isProfanity(str: string) {
    try {
      const scores: number[] = []

      console.log('h#')

      const idk = await index.query({
        topK: 1,
        data: str,
      })

      const score = idk.map(({ score }) => score)

      scores.push(...score)

      console.log('done waiting', scores)

      const highestScore = scores.sort().reverse()[0]

      console.log(highestScore)

      const isProfanity = highestScore! > 0.85

      return { score: highestScore, isProfanity }
    } catch (err) {
      console.log(err)
    }
  }

  return c.json(result)
})

export default app
